export default {
	data() {
		return {
			grade:0,
			message:'',
			url:'',
			orderId:0,
			order:'',
			name:''
		}
	},
	onLoad(e) {
		var _this = this;
		this.orderId = e.orderId;
		this.url = this.serverURL;
		this.name = e.name;
	},
	methods: {
		settGrade(num) {
			this.grade = num;
		},
		goHome() {
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
		write() {
			var _this = this;
			uni.showLoading({
				title:'评论中...',
				mask:true
			})
			uni.request({
				url: _this.url + 'home/Order/update_transportation_order',
				method:'POST',
				data:{
					id: _this.orderId,
					comment_num: _this.grade,
					comment_msg:_this.message
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						uni.hideLoading();
						uni.showToast({
							title:'评论完成',
							icon:"success",
							mask:true
						});
						setTimeout(function(){
							uni.navigateBack();
						},1500)
					}
				},
				fail(error) {
					console.log(error);
					uni.showToast({
						title:'评论失败',
						icon:"none",
						mask:true
					})
				}
			})
		}
	}
}
